<style>
.p-t {
  margin-bottom: 10px;
}

.u-btn-wrap {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}

.file-name-wrap {
  width: 200px;
  height: 32px;
  line-height: 32px;
  overflow: hidden;
  border: 1px solid #ececec;
  border-radius: 4px;
  padding: 0 10px;
  position: relative;
  top: 5px;
}

.label-wrap {
  border: 1px solid #ececec;
  border-radius: 4px;
  position: relative;
  top: 5px;
  height: 32px;
  line-height: 32px;
}

.label-wrap > .excel-file-label {
  line-height: 32px;
  cursor: pointer;
  padding: 7px 10px;
}

.label-wrap > .excel-file-label:hover {
  color: #fff;
  background-color: #66b1ff;
  border-radius: 4px;
}
</style>

<template>
  <div>
    <el-dialog :title="title" :visible.sync="isShow" width='450px'>
      <el-form ref="form" label-width="80px">
        <el-form-item label="本地上传">
          <div class="excel-u-d-wrap">
            <div class="u-btn-wrap">
              <div class="file-name-wrap">{{ file.name }}</div>
              <div class="label-wrap">
                <label for="excelFile" class="excel-file-label">
                  <input type="file" id="excelFile" hidden @change="handleFileSelect"/>
                  <span class="u-txt">选择文件</span>
                </label>
              </div>
            </div>
            <div class="d-btn-wrap">
              <span>请按EXCEL模板格式导入</span>
              <el-button type="text" @click="downloadModel" size="mini" :disabled="isDisabled">下载模板</el-button>
            </div>
          </div>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="isShow = false" size="mini">取消</el-button>
        <el-button type="primary" @click="doConfirmHandel" size="mini" :disabled="!file.name || conDisabled" :loading="saveLoading">确定</el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
import * as utils from '@/utils/utils'
import exportSheet from '@/utils/exportSheet'
const requestUrl = {
  // 批量添加
  batchInsert: '/plateform/a/product/goods/batchInsert'
}
export default {
  data () {
    return {
      conDisabled: false, // 防止多次点击确定按钮
      isDisabled: false, // 下载模板按钮防止多次点击
      saveLoading: false,
      title: '批量添加',
      isShow: false,
      formLabelWidth: '70px',
      file: {name: ''}, // 导入数据
      // 导入商品
      exportGoods: [],
      tempConfig: {
        skuNo: 'ERP商品编码（必填）',
        ratio: '转换比例（必填，卖1份需减扣多少单位的母商品）',
        skuName: '商品名称（必填，限制30个汉字）',
        spec: '规格（必填）',
        buyName: '商品卖点（选填，商品特色、优点，限制20个字）',
        weight: '重量（必填，整数，单位：g）',
        categoryId1: '一级分类id（必填）',
        categoryId2: '二级分类id（没有二级分类可不填，有则必填）',
        sortNum: '排序（0-999的整数，数值越大越靠前，必填）',
        origin: '产地（选填）',
        qualityPeriod: '保质期（选填）',
        pack: '包装（选填）',
        images: '商品方图url（选填，多张逗号区分）',
        detailImages: '商品详情图url（选填）'
      },
      // 批量添加商品集合
      list: []

    }
  },
  methods: {
    /**
     * [setDefaultMesg 设置弹框默认属性]
     * @param {[type]} _params [自定义参数]
     */
    setDefaultMesg (_params) {
      this.isShow = true
      if (_params.title) {
        this.title = _params.title
      }
      // 清除当前选择的文件信息
      this.file = {name: ''}
      let excelFileDom = document.getElementById('excelFile')
      if(excelFileDom) {
        excelFileDom.value = ""
      }
    },
    /**
     * [downloadModel 下载模板]
     * @return {[type]} [description]
     */
    downloadModel () {
      this.isDisabled = true
      let dataArr = [
        {
          skuNo: '',
          ratio: '',
          skuName: '',
          spec: '',
          buyName: '',
          weight: '',
          categoryId1: '',
          categoryId2: '',
          sortNum: '',
          origin: '',
          qualityPeriod: '',
          pack: '',
          images: '',
          detailImages: ''
        }
      ]
      exportSheet.exportFileByConfig(dataArr, '批量添加线上商品库商品模板.xlsx', this.tempConfig)
      this.$notify.success({
        title: '温馨提示',
        message: '批量添加线上商品库商品模板下载成功'
      })
      setTimeout(() => {
        this.isDisabled = false
      }, 500)
    },
    /**
     * [handleFileSelect 选择文件发生变化]
     * @param  {[type]} e [description]
     * @return {[type]}   [description]
     */
    handleFileSelect (e) {
      // 点击取消不做任何变化
      if (!e.target.files.length) {
        return
      }
      this.file = e.target.files[0] || {name: ''}
      let idx = this.file.name.lastIndexOf(".")
      let strLength = this.file.name.length
      let postf = this.file.name.substring(idx, strLength) // 后缀名

      if (!this.file.name || postf != '.xlsx') {
        this.$notify.warning({
          title: '提示',
          message: '请选择excel文件'
        })
        // 清除当前选择的文件信息
        this.file = {name: ''}
        document.getElementById('excelFile').value = ""
        return
      }

      let items = []
      let parseConfig = {}
      for (let key in this.tempConfig) {
        parseConfig[this.tempConfig[key]] = key
      }
      items = exportSheet.parseToJSON(this.file, parseConfig)
      items.then((result) => {
        // 过滤空行
        let resultVal = result.filter(item => !!item.skuNo)
        let listArrTemp = []
        let numReg = /^[1-9]*[1-9][0-9]*$/
        let ratioReg = /((^[1-9]\d*)|^0)(\.\d{0,3}){0,1}$/
        for (let item of resultVal) {
          if (!item.skuNo) {
            this.$notify.warning({
              title: '提示',
              message: `请填写ERP商品编码`
            })
            // 清除当前选择的文件信息
            this.file = {name: ''}
            document.getElementById('excelFile').value = ""
            return
          }

          if (!item.ratio) {
            this.$notify.warning({
              title: '提示',
              message: `请填写转换比例`
            })
            // 清除当前选择的文件信息
            this.file = {name: ''}
            document.getElementById('excelFile').value = ""
            return
          }else {
            if(!ratioReg.test(item.ratio)) {
              this.$notify.warning({
                title: '提示',
                message: `转换比例是大于0，小数保留小数点后3位`
              })
              // 清除当前选择的文件信息
              this.file = {name: ''}
              document.getElementById('excelFile').value = ""
              return 
            }else {
              if(parseFloat(item.ratio) > 10) {
                this.$notify.warning({
                  title: '提示',
                  message: `转换比例大于10，添加失败`
                })
                // 清除当前选择的文件信息
                this.file = {name: ''}
                document.getElementById('excelFile').value = ""
                return 
              }
            }
          }

          if (!item.skuName) {
            this.$notify.warning({
              title: '提示',
              message: `请填写商品名称`
            })
            // 清除当前选择的文件信息
            this.file = {name: ''}
            document.getElementById('excelFile').value = ""
            return
          }else {
            if(item.skuName.length > 30) {
              this.$notify.warning({
                title: '提示',
                message: `商品名称,限制30个汉字`
              })
              // 清除当前选择的文件信息
              this.file = {name: ''}
              document.getElementById('excelFile').value = ""
              return 
            }
          }

          if (!item.spec) {
            this.$notify.warning({
              title: '提示',
              message: `请填写商品规格`
            })
            // 清除当前选择的文件信息
            this.file = {name: ''}
            document.getElementById('excelFile').value = ""
            return
          }

          if (item.buyName) {
            if(item.buyName.length > 20) {
              this.$notify.warning({
                title: '提示',
                message: `商品卖点信息,限制20个字`
              })
              // 清除当前选择的文件信息
              this.file = {name: ''}
              document.getElementById('excelFile').value = ""
              return 
            }
          }

          if (!item.weight) {
            this.$notify.warning({
              title: '提示',
              message: `请填写商品重量`
            })
            // 清除当前选择的文件信息
            this.file = {name: ''}
            document.getElementById('excelFile').value = ""
            return
          }else {
            if(numReg.test(item.weight)) {
              if(item.weight <= 0) {
                this.$notify.warning({
                  title: '提示',
                  message: `商品重量为大于0的整数`
                })
                // 清除当前选择的文件信息
                this.file = {name: ''}
                document.getElementById('excelFile').value = ""
                return
              }
            }else {
              this.$notify.warning({
                title: '提示',
                message: `商品重量大于0的整数`
              })
              // 清除当前选择的文件信息
              this.file = {name: ''}
              document.getElementById('excelFile').value = ""
              return
            }
          }

          if (!item.categoryId1) {
            this.$notify.warning({
              title: '提示',
              message: `请填写一级分类id`
            })
            // 清除当前选择的文件信息
            this.file = {name: ''}
            document.getElementById('excelFile').value = ""
            return
          }

          if(item.sortNum == "") {
            item.sortNum = 0
          }else if(item.sortNum == 0) {
            item.sortNum = 0
          }else {
            if(numReg.test(item.sortNum)) {
              if(item.sortNum < 0 || item.sortNum > 999) {
                this.$notify.warning({
                  title: '提示',
                  message: `店内分类下的商品排序，0-999的整数，数值越大越靠前`
                })
                // 清除当前选择的文件信息
                this.file = {name: ''}
                document.getElementById('excelFile').value = ""
                return
              }
            }else {
              this.$notify.warning({
                title: '提示',
                message: `店内分类下的商品排序，0-999的整数，数值越大越靠前`
              })
              // 清除当前选择的文件信息
              this.file = {name: ''}
              document.getElementById('excelFile').value = ""
              return
            }
          }
          if(item.images) {
            item.imageList = item.images.replace(/\，/g, ',').split(',')
          }
          if(item.detailImages) {
            item.detailImageList = item.detailImages.replace(/\，/g, ',').split(',')
          }
          listArrTemp.push(item)
        }
        
        this.list = listArrTemp
      }).catch()
    },
    /**
     * [doConfirmHandel 点击确定]
     * @return {[type]} [description]
     */
    doConfirmHandel() {
      this.conDisabled = true
      this.saveLoading = true
      this.$http.post(requestUrl.batchInsert, {list: this.list}).then(res => {
        setTimeout(() => {
          this.conDisabled = false
          this.saveLoading = false
        }, 800)
        if(res.code === 0) {
          this.$message.success("批量添加成功")
          this.isShow = false
          this.$emit("doSearchHandel")
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.conDisabled = false
        this.saveLoading = false
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    } 
  }
}
</script>



