<style>
  .p-m-w {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  .img-w {
    width: 80px;
    height: 80px;
  }
  .p-m-c-r {
    padding: 0 10px;
  }
  .erp-p-no {
    color: #666;
    font-size: 12px;
  }
  .a-s-p-i {
    display: inline-block;
  }

  .b-add-img-btn-w {
    cursor: pointer;
    width: 80px;
    height: 80px;
    border: 1px solid #a2a2a2;

    color: #a2a2a2;
    font-size: 20px;
    font-weight: bold;
    text-align: center;
    line-height: 80px; 
  }
  .b-add-img-w {
    cursor: pointer;
    width: 80px;
    height: 80px;
  }
  .b-add-img-w>img {
    width: 80px;
  }
</style>

<template>
  <div>
    <!-- 操作框 -->
    <div class="handel-search-wraper">
      <div class="handel-wraper">
        <el-button type="primary" @click="goAddGoodsPage" size="mini" icon="el-icon-plus">添加商品</el-button>
        <el-button type="primary" size="mini" icon="el-icon-plus" @click="batchAddGoodsShow">批量添加商品</el-button>
      </div>
      <hr class="s-slip">
      <div class="search-form-wraper">
        <el-form :model="searchContent" label-position="top">
          <el-form-item label="商品分类">
            <el-cascader
              placeholder="请选择分类"
              class="search-input"
              :options="categoryList"
              change-on-select
              size="small"
              clearable
              filterable
              @change="doCategorysChangeHandel"
              :props="cateDefaultProps"
              v-model="searchContent.categoryIdList">
            </el-cascader>
          </el-form-item>

          <el-form-item label="SKU编码/商品名称">
            <goods-select ref="GoodsSelect" @DoSelectGoodsHandel="DoSelectGoodsHandel"></goods-select>
          </el-form-item>

          <el-form-item label="ERP编码">
            <el-input class="search-input" v-model.trim="searchContent.skuNo" placeholder="ERP编码" size="small" @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
          </el-form-item>

          <el-form-item label="商品类型">
            <el-select class="search-input" v-model="searchContent.goodsType" placeholder="请选择" size="small" @change="doSearchHandel(true)">
              <el-option :label="item.label" :value="item.value" :key="item.value" v-for="item in goodsTypeOptions"></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="母商品种类">
            <el-select class="search-input" v-model="searchContent.skuType" placeholder="请选择" size="small" @change="doSearchHandel(true)">
              <el-option :label="item.label" :value="item.value" :key="item.value" v-for="item in skuTypeOptions"></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="区域">
            <el-select 
              class="search-input" 
              v-model="searchContent.regionCode" 
              filterable 
              placeholder="请选择区域" 
              size="small" 
              @change="doSearchHandel(true)">
              <el-option
                v-for="item in regionOptions"
                :key="item.storeGroupNo"
                :label="item.storeGroupName"
                :value="item.storeGroupNo">
              </el-option>
            </el-select>
          </el-form-item>
          <!-- <el-form-item label="仅查看本城市可售">
            <el-select class="search-input" v-model="searchContent.byCityId" placeholder="请选择" size="small" @change="doSearchHandel(true)">
              <el-option :label="item.label" :value="item.value" :key="item.value" v-for="item in byCityIdOptions"></el-option>
            </el-select>
          </el-form-item> -->

          <el-form-item label="扫码捡货">
            <el-select class="search-input" v-model="searchContent.pick" placeholder="请选择" size="small" @change="doSearchHandel(true)">
              <el-option :label="item.label" :value="item.value" :key="item.value" v-for="item in refundAndPickOptions"></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="差额退款">
            <el-select class="search-input" v-model="searchContent.refund" placeholder="请选择" size="small" @change="doSearchHandel(true)">
              <el-option :label="item.label" :value="item.value" :key="item.value" v-for="item in refundAndPickOptions"></el-option>
            </el-select>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
            <el-button type="primary" @click="exportData" size="mini" :loading="exportLoading">导出</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>

    <!-- 搜索结果 -->
    <div class="table-wraper">
      <div class="batch-handel-w">
        <el-button class="sup-mini" size="mini" icon="el-icon-edit" :disabled="checkedList.length === 0" @click="showChangeCatDialog">批量改分类</el-button>
        <el-button class="sup-mini" size="mini" icon="el-icon-delete" :disabled="checkedList.length === 0"  @click="deleteGoods">批量删除</el-button>
      </div>
      <el-table
        class="fixed-height"
        ref="multipleTable"
        v-loading="loading"
        :data="tableData"
        empty-text="没有查询到您想要的数据"
        style="width:100%;"
        @select="handleSelect"
        @select-all="handleSelectAll"
        size="small">
        <el-table-column
          align="center"
          type="selection">
        </el-table-column>
        <el-table-column min-width="240px" label="商品信息">
          <template slot-scope="scope">
            <div class="p-m-w">
              <div class="img-w">
                <div class="b-add-img-w" v-if="scope.row.imgUrl && scope.row.imgUrl != ''" @click="addGoodsImgShow(scope.row)">
                  <img :src="scope.row.imgUrl" alt="" onerror="onerror=null;src='/shop/static/img_error.png'">
                </div>
                <div v-else class="b-add-img-btn-w" @click="addGoodsImgShow(scope.row)">
                  <i class="el-icon-plus"></i>
                </div>
              </div>
              <div class="p-m-c-r">
                <div>{{scope.row.skuName || '暂无'}}</div>
                <div class="erp-p-no">SKU编码：{{scope.row.goodsNo || '--'}}</div>
                <div>
                  <el-popover
                    placement="bottom-start"
                    trigger="click">
                    <el-table
                      v-loading="visiblePopoverLoading"
                      :data="goodsErpSkuRelationsData"
                      empty-text="暂无数据"
                      size="small">
                      <el-table-column width="80" prop="skuNo" label="ERP编码"></el-table-column>
                      <el-table-column width="150" prop="skuName" label="母商品名称"></el-table-column>
                      <el-table-column width="90" prop="categoryName" label="ERP分类"></el-table-column>
                      <el-table-column width="90" prop="skuType" label="商品种类" :formatter="skuTypeFormater"></el-table-column>
                      <el-table-column width="80" prop="ratio" label="转换比例"></el-table-column>
                      <el-table-column width="60" prop="saleUnitName" label="单位"></el-table-column>
                    </el-table>
                    <el-button slot="reference" type="text" size="mini" @click="showErpSkuRelation(scope.row)">查看母商品</el-button>
                  </el-popover>
                </div>
              </div>
            </div>
          </template>     
        </el-table-column>
        <el-table-column min-width="120px" prop="spec" label="规格"></el-table-column>
        <el-table-column min-width="120px" prop="categoryName" label="分类"></el-table-column>
        <el-table-column label="区域可售" min-width="200px">
          <template slot-scope="scope">
            <div v-if="!scope.row.regionName">
              --
            </div>
            <div v-else v-for="(item, idx) of scope.row.regionName" class="a-s-p-i" :key="idx">
              <span>{{item}}</span> 
              <span v-show="(idx+1) != scope.row.regionName.length">/</span>&nbsp;
            </div>
          </template>
        </el-table-column>
        <!-- <el-table-column label="本城市在售渠道" min-width="150px">
          <template slot-scope="scope">
            <div v-if="!scope.row.platformName">
              --
            </div>
            <div v-else v-for="(item, idx) of scope.row.platformName" class="a-s-p-i" :key="idx">
              <span>{{item}}</span> 
              <span v-show="(idx+1) != scope.row.platformName.length">/</span>&nbsp;
            </div>
          </template>
        </el-table-column> -->
        
        <el-table-column min-width="80px" prop="sortNum" label="排序"></el-table-column>
        <el-table-column
          align="center"
          fixed="right"
          label="操作"
          width="80">
          <template slot-scope="scope" size="small">
            <div>  
              <el-button @click="editItem(scope.row)" type="text" size="mini" icon="el-icon-edit">编辑</el-button>
            </div>
            <!-- <div>
              <el-button @click="deleteItem(scope.row)" type="text" size="mini" icon="el-icon-delete">删除</el-button>
            </div> -->
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页条 -->
      <el-pagination
        v-if="total>0"
        class="tc mt20"
        background
        @size-change="changeSize"
        @current-change="changeCurrent"
        :current-page.sync="pageNum"
        :page-sizes="pageSizes"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
    
    <el-dialog title="批量改分类" :visible.sync="dialogFormVisible" width='460px'>
      <el-form>
        <el-form ref="form" label-width="60px">
          <el-form-item label="分类" required>
            <el-cascader
              placeholder="请选择分类"
              class="search-date-picker"
              :options="categoryList"
              size="small"
              clearable
              filterable
              :props="cateDefaultProps"
              v-model="categoryPlatList">
            </el-cascader>
          </el-form-item>
        </el-form>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="doConfirmHandel" size="mini">确 定</el-button>
      </div>
    </el-dialog>

    <batch-add-goods ref="batchAddGoods" @doSearchHandel="doSearchHandel(true)"></batch-add-goods>
    <add-goods-img ref="addGoodsImg" @doSearchHandel="doSearchHandel(true)"></add-goods-img> 
  </div>
</template>

<script>
  import Storage from '@/utils/localStorage'
  import * as utils from '@/utils/utils'
  import GoodsSelect from "@/components/Common/GoodsSelectByGid"
  import BatchAddGoods from './BatchAddGoods'
  import AddGoodsImg from './AddGoodsImg'
  import exportSheet from '@/utils/exportSheet'
  import config from '@/utils/config'
  
  const mallGoodsMgt = {
    // 获取三方平台店内分类
    getCategoryTree: '/plateform/a/category/getCategoryTree',
    // 商城商品(News)——商品库管理
    librarySearch: '/plateform/a/product/goods/search',
    // 商品库管理——删除
    delProduct: '/plateform/a/product/goods/del',
    // 批量修改分类
    batchUpdateGoodsCategory: '/plateform/a/product/goods/batchUpdateGoodsCategory',
    // 商品选择-商品列表
    selectGoods: '/plateform/a/product/goods/selectGoods',
    // 批量导出
    exportData: '/plateform/a/product/goods/export',
    // 查看母商品
    goodsItems: '/plateform/a/product/goods/goodsItems',
    // 查询区域店组一级列表
    areaStoreGroupList: '/foundation/storeGroup/areaStoreGroupList'
  }
  
  export default {
    data() {
      return {
        pageSizes: config.pageSizes10,
        exportLoading: false, // 导出loading
        loading: false,
        pageNum: 1,
        pageSize: 10,
        total: 0,
        searchContent: {
          pick: -1,
          refund: -1,
          skuNo: '', // ERP编码
          skuType: '', // 母商品类型：C-组合商品；Q-计量商品；S-标准商品；W-重量商品
          goodsType: '', // 商品类型：1-常规商品；2-组合商品
          byCityId: false, // 仅查看本城市可售
          queryString: '', // 输入的查询条件
          goodsIds: [], // 全国商品ID 集合
          categoryIdList: [],
          categoryId: '', // 商品分类ID
          // 区域Id
          regionCode: ''
        },
        // 默认分类参数
        cateDefaultProps: {
          children: 'categoryVoList',
          label: 'categoryName',
          value: 'categoryId'
        },
        tableData: [], // 表格中查询结果数据
        categoryList: [], // 商品分类id
        // 选中的商品 供批量操作使用
        checkedList: [],
        // 批量改分类的弹窗
        dialogFormVisible: false,
        // 批量改分类 用户选择的分类
        categoryPlatList: [],

        // 仅查看本城市可售 选择
        byCityIdOptions: [
          {
            label: '是',
            value: true
          },
          {
            label: '否',
            value: false
          }
        ],
        // 商品类型：1-常规商品；2-组合商品
        goodsTypeOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '常规商品',
            value: 1
          },
          {
            label: '组合商品',
            value: 2
          }
        ],
        // 母商品类型：C-组合商品；Q-计量商品；S-标准商品；W-重量商品
        skuTypeOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '重量商品',
            value: 'W'
          },
          {
            label: '标准商品',
            value: 'S'
          },
          {
            label: '计量商品',
            value: 'Q'
          },
          {
            label: '组合商品',
            value: 'C'
          }
        ],
        // 导出配置
        exportConfig: {
          goodsNo: 'SKU编码',
          skuNo: '母商品ERP编码',
          skuName: '商品名称',
          spec: '商品规格',
          imgListStr: '商品方图（多个逗号分隔）',
          goodsDetailImg: '商品详情图',
          regionNameStr: 'ERP可售城市',
          platformNameStr: '本城市在售渠道',
          buyName: '商品卖点',
          weight: '重量（g）',
          categoryName: '所属分类',
          sortNum: '排序',
          origin: '产地',
          qualityPeriod: '保质期',
          pack: '包装'
        },

        // 弹窗是否显示
        visiblePopoverLoading: false,
        currentGoodsId: '',
        // 查看母商品信息数据
        goodsErpSkuRelationsData: [],

        refundAndPickOptions: [
          {
            label: '全部',
            value: -1
          },
          {
            label: '未开启',
            value: 0
          },
          {
            label: '已开启',
            value: 1
          }
        ],
        regionOptions: []
      }
    },
    created() {
      // 三方平台店内分类
      this.getCategoryId()
      // 获取区域  无权限控制
      this.areaStoreGroupList()
    },
    mounted() {
      let searchContentMesg = Storage.get("searchContent")
      if(this.$route.meta.keepAlive && searchContentMesg) {
        this.pageNum = searchContentMesg.pageNum
        this.searchContent = searchContentMesg.searchContent
      }
      this.doSearchHandel()
      Storage.remove('searchContent')
      // 商品筛选组件初始化
      this.goodsSelectInit()
    },
    components: {
      "goods-select": GoodsSelect,
      "batch-add-goods": BatchAddGoods,
      "add-goods-img": AddGoodsImg
    },
    methods: {
      /**
       * [goAddGoodsPage 到添加商品页面 获取SKU列表]
       * @return {[type]} [description]
       */
      goAddGoodsPage() {
        this.$router.push("/mall_goods_mgt/add_goods")
      },
      /**
       * [getCategoryId 三方平台店内分类]
       * @return {[type]} [description]
       */
      getCategoryId() {
        this.$http.post(mallGoodsMgt.getCategoryTree, {DIFFCITY: true}).then(res => {
          if(res.code === 0) {
            if(res.data && res.data.length) {
              for(let item of res.data) {
                if(item.categoryVoList && item.categoryVoList.length === 0) {
                  item.categoryVoList = null
                }
              }
              this.categoryList = res.data
            }else {
              this.categoryList = []
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(三方平台店内分类):", e.message)
        })
      },
      /**
       * [doCategorysChangeHandel 分类发生变化]
       * @return {[type]} [description]
       */
      doCategorysChangeHandel() {
        // 商品筛选组件初始化
        this.goodsSelectInit()

        this.doSearchHandel(true)
      },
      /**
       * [goodsSelectInit 商品筛选组件初始化]
       * @return {[type]} [description]
       */
      goodsSelectInit() {
        if(this.$refs.GoodsSelect) {
          let _val = {
            queryString: this.searchContent.queryString,
            categoryIdList: this.searchContent.categoryIdList,
            goodsIds: this.searchContent.goodsIds
          }
          this.$refs.GoodsSelect.setDefaultMesg(_val)
        }
      },
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSize (value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [changeCurrent 点击分页条]
       * @param    {[type]}                 value [当前页]
       * @return   {[type]}                       [description]
       */
      changeCurrent (value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      /**
       * [doSearchHandel 查询列表接口]
       * @param  {[type]} _param [description]
       * @return {[type]}         [description]
       */
      doSearchHandel(_param) {
        this.checkedList = []
        let spliceLen = this.searchContent.categoryIdList.length - 1
        this.searchContent.categoryId = this.searchContent.categoryIdList[spliceLen]
        this.loading = true
        if(_param === true) {
          this.pageNum = 1
        }
        this.$http.post(mallGoodsMgt.librarySearch, { ...this.searchContent, pageNum: this.pageNum, pageSize: this.pageSize, DIFFCITY: true }).then(res => {
          this.loading = false
          if(res.code === 0) {
            // 获取表格数据
            if(res.data && res.data.result && res.data.result.length) {
              for(let item of res.data.result) {
                item.uniqueId = `${item.id}` + `${item.skuNo}`
              }
              this.tableData = res.data.result            
            }else {
              this.tableData = []
            }

            this.total = res.data.total
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [editItem 编辑]
       * @param  {[type]} _row [行信息]
       * @return {[type]}      [description]
       */
      editItem(_row) {
        // 设置搜索条件
        let _paramMesg = {
          searchContent: this.searchContent,
          pageNum: this.pageNum
        }
        Storage.set("searchContent", _paramMesg)
        this.$router.push('/mall_goods_mgt/edit_goods/' + `${_row.id}`)
      },
      /**
       * [deleteItem 删除]
       * @param  {[type]} _row [行信息]
       * @return {[type]}      [description]
       */
      deleteItem(_row) {
        let goodsIds = []
        goodsIds.push(_row.id)
        this.$confirm('您确定要删除所选商品吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http.post(mallGoodsMgt.delProduct, {goodsIds: goodsIds, DIFFCITY: true}).then(res => {
            if(res.code === 0) {
              this.$message.success("删除成功")
              this.doSearchHandel()
              return 
            }
            throw new Error(res.msg)
          }).catch(e => {
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {})
      },
      /**
       * [deleteGoods 批量删除商品]
       * @return {[type]} [description]
       */
      deleteGoods() {
        if(this.checkedList === 0) {
          this.$message("请选择要删除商品")
          return
        }
        let goodsIds = []
        for(let item of this.checkedList) {
          goodsIds.push(item.id)
        }

        this.$confirm('您确定要删除所选商品吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http.post(mallGoodsMgt.delProduct, {goodsIds: goodsIds, DIFFCITY: true}).then(res => {
            if(res.code === 0) {
              this.$message.success("批量删除成功")
              // 重置选中数据
              this.checkedList = []
              this.doSearchHandel()
              return 
            }
            throw new Error(res.msg)
          }).catch(e => {
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {})
      },
      /**
       * [handleSelect 选择某一个]
       * @return {[type]} [description]
       */
      handleSelect(val, row) {
        let hash = {}
        let checkedListTemp = []

        if(utils.isKeyInArray(row.uniqueId, this.checkedList)) {
          this.checkedList = utils.removeItemByKeyVal('uniqueId', row.uniqueId, this.checkedList)
          return
        }else {
          checkedListTemp.push(row)
        }

        let checkedListTempArr = [...checkedListTemp, ...this.checkedList]
        this.checkedList = checkedListTempArr.reduceRight((item, next) => {
          hash[next.uniqueId] ? '' : hash[next.uniqueId] = true && item.push(next)
          return item
        }, [])
      },
      /**
       * [handleSelectAll 选择多个]
       * @return {[type]} [description]
       */
      handleSelectAll(val) {
        let hash = {}
        if(val.length > 0) {        
          // 全选
          for(let item of val) {
            this.checkedList.push(item)
          }
          // 去重
          this.checkedList = this.checkedList.reduceRight((item, next) => {
            hash[next.uniqueId] ? '' : hash[next.uniqueId] = true && item.push(next)
            return item
          }, [])
        }else { 
          // 全不选 此时val.length<=0
          // 1.获取当前table中每一条的id
          let checkedListTemp = []
          for(let item of this.tableData) {
            checkedListTemp.push(item)
          }
          // 2.从要传递的参数中去掉这些重复的
          let checkedListArr = utils.getSubtract(this.checkedList, checkedListTemp, 'uniqueId')
          this.checkedList = checkedListArr && checkedListArr.length > 0 ? checkedListArr : [] 
        }
      },
      /**
       * [showChangeCatDialog 批量改分类]
       * @return {[type]} [description]
       */
      showChangeCatDialog() {
        if(this.checkedList === 0) {
          this.$message("请选择要修改分类的商品")
          return
        }
        // 重置数据
        this.categoryPlatList = []
        this.dialogFormVisible = true
      },
      /**
       * [doConfirmHandel 点击确认修改分类]
       * @return {[type]} [description]
       */
      doConfirmHandel() {
        // 分类
        let lenNum = this.categoryPlatList.length - 1
        let categoryId = this.categoryPlatList[lenNum]
        // 选中的商品
        let goodsIds = []
        for(let item of this.checkedList) {
          goodsIds.push(item.id)
        }
        if(!categoryId) {
          this.$message("请选择分类")
          return
        }

        let _params = {
          categoryId: categoryId,
          goodsIds: goodsIds, 
          DIFFCITY: true
        }

        this.$http.post(mallGoodsMgt.batchUpdateGoodsCategory, _params).then(res => {
          if(res.code === 0) {
            this.dialogFormVisible = false
            this.$message.success("批量修改分类成功")
            // 重置选中数据
            this.checkedList = []

            this.doSearchHandel()
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [exportData 批量导出]
       * @return {[type]} [description]
       */
      exportData() {
        this.exportLoading = true
        this.$http.post(mallGoodsMgt.exportData, {...this.searchContent, pageNum: 0, pageSize: 0, DIFFCITY: true}).then(res => {
          this.exportLoading = false
          if(res.code === 0) {
            if(res.data && res.data.result && res.data.result.length) {
              for(let item of res.data.result) {
                item.regionNameStr = item.regionName ? item.regionName.join(',') : ''
                item.platformNameStr = item.platformName ? item.platformName.join(',') : ''
                // 商品方图
                item.imgListStr = item.imgs && item.imgs.length ? item.imgs.join(',') : ''
                // 商品详情图
                item.goodsDetailImg = item.detailImgs && item.detailImgs.length ? item.detailImgs.join(',') : ''
              }
              let tableDataArr = res.data.result
              exportSheet.exportFileByConfig(tableDataArr, utils.timestampToTimeNoHMS(Date.parse(new Date())) + '_线上商品库.xlsx', this.exportConfig)
              this.$notify.success({
                title: '温馨提示',
                message: `${utils.timestampToTimeNoHMS(Date.parse(new Date()))}_线上商品库导出成功`
              });
            }else {
              this.$notify.warning({
                title: '温馨提示',
                message: '没有查询到要导出的数据'
              });
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.exportLoading = false
          this.$notify.error({
            title: '提示(导出数据)：',
            message: e.message
          })
        })
      },
      /**
       * [batchAddGoodsShow 批量添加弹框显示]
       * @return {[type]} [description]
       */
      batchAddGoodsShow() {
        if(this.$refs.batchAddGoods) {
          let _params = {
            title: '批量添加'
          }
          this.$refs.batchAddGoods.setDefaultMesg(_params)
        }
      },
      /**
       * [showErpSkuRelation 获取母商品信息]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      showErpSkuRelation(_row) {
        this.visiblePopoverLoading = true
        let goodsId = _row.id
        this.$http.post(mallGoodsMgt.goodsItems, {goodsId: goodsId, DIFFCITY: true}).then(res => {
          this.visiblePopoverLoading = false
          if(res.code === 0) {
            if(res.data && res.data.length) {
              this.goodsErpSkuRelationsData = res.data
            }else {
              this.goodsErpSkuRelationsData = []
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.visiblePopoverLoading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [skuTypeFormater 表格数据格式化]
       * @param  {[type]} row       [description]
       * @param  {[type]} column    [description]
       * @param  {[type]} cellValue [description]
       * @return {[type]}           [description]
       */
      skuTypeFormater(row, column, cellValue) {
        if(cellValue === 'C') {
          return "组合商品"
        }else if(cellValue == 'Q') {
          return "计量商品"
        }else if(cellValue == 'S') {
          return "标准商品"
        }else if(cellValue == 'W') {
          return "重量商品"
        }else {
          return "--"
        }
      },
      /**
       * [addGoodsImgShow 添加商品图片]
       * @param {[type]} _row [description]
       */
      addGoodsImgShow(_row) {
        let goodsId = _row.id
        if(this.$refs.addGoodsImg) {
          let _params = {
            title: '添加商品图片',
            goodsId: goodsId
          }
          this.$refs.addGoodsImg.setDefaultMesg(_params)
        }
      },
      /**
       * [DoSelectGoodsHandel 确认选择]
       */
      DoSelectGoodsHandel(_params) {
        let checkedList = _params.checkedList
        let queryString = _params.queryString
        let goodsIds = []
        for(let item of checkedList) {
          goodsIds.push(item.goodsId)
        }
        this.searchContent.goodsIds = goodsIds
        this.searchContent.queryString = queryString
        this.doSearchHandel(true)
      },
      /**
       * @description: 获取区域  无权限控制
       * @param {type} 
       * @return: 
       */
      areaStoreGroupList() {
        this.$http.get(mallGoodsMgt.areaStoreGroupList).then(res => {
          if(res.code === 0) {
            this.regionOptions = res.data && res.data.length ? res.data : []
            this.regionOptions.unshift({
              storeGroupNo: '',
              storeGroupName: '全部'
            })
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(获取门店列表):", e.message)
        })
      }

    },
    /**
     * [beforeRouteLeave 页面离开]
     * @param  {[type]}   to   [description]
     * @param  {[type]}   from [description]
     * @param  {Function} next [description]
     * @return {[type]}        [description]
     */
    beforeRouteLeave (to, from, next){
      from.meta.keepAlive = false;
      next();
    }

  }
</script>


