<style>
  @import "../../../../static/style/file_upload.css";
  .draggable-img-wrap {
    overflow: hidden;
    min-height: 110px;
  }
  .mh-up-w-draggable {
    float: left;
    margin: 0 5px;
  }
  .mh-up-d-w-btn-w {
    padding: 0 5px;
    line-height: 0;
    margin-top: 8px;
    margin-bottom: 8px;
    text-align: center;
  }
  .up-h-i {
    cursor: pointer;
    padding-left: 6px;
    padding-right: 6px;
  }
  .up-h-i:hover {
    color: #3080fe;
  }
</style>

<template>
  <div>
    <el-dialog :title="title" :visible.sync="isShow" width='524px'>
      <div class="draggable-img-wrap" v-loading="loading">
        <draggable v-model="imageListArr" @update="datadragEnd">
          <transition-group>
            <div class="mh-up-w-draggable" v-for="(item, idx) of imageListArr" @click="setItemIdx(idx)" :key="item.imgUrl">
              <el-upload
                class="avatar-uploader"
                action=""
                :show-file-list="false"
                :before-upload="imageUploadRegForMore"
                :http-request="emitUpload">
                <div v-loading="imageUploadLoading && currentUpFileIdx == idx">
                  <img v-if="item.imgUrl" :src="item.imgUrl" class="avatar">
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </div>
              </el-upload>
              <div class="mh-up-d-w-btn-w" v-if="item.imgUrl && item.imgUrl != ''"> 
                <span class="up-h-i" @click="deleteUpFile(idx)"><i class="el-icon-delete"></i></span>
                <span class="up-h-i"><i class="el-icon-rank"></i></span>
              </div>
            </div>
          </transition-group>
        </draggable>
      </div>
      <p>请上传大小不超过230KB的800*800像素的JPG或PNG图片，最多可添加6张</p>
      <div slot="footer" class="dialog-footer">
        <el-button @click="isShow = false" size="mini">取消</el-button>
        <el-button type="primary" @click="doConfirmHandel" size="mini" :disabled="disabled" :loading="saveLoading">确定</el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
import Draggable from 'vuedraggable'
import * as utils from '@/utils/utils'
import * as qiniu from 'qiniu-js'
import Storage from '@/utils/localStorage'
const fileUrlList = {
  getQiNiuToken: '/foundation/qiniu/auth/file'
}
const requestUrl = {
  // 商品库管理——编辑或新增 (完善商品资料)
  updateOrInsert: '/plateform/a/product/goods/updateorinsert',
  // 商品库管理——完善商品资料
  libraryDetail: '/plateform/a/product/goods/detail'
}
export default {
  data () {
    return {
      loading: false,
      disabled: false,
      saveLoading: false,
      title: '添加商品图片',
      goodsId: '',
      isShow: false,
      addParamMesg: {
        DIFFCITY: true,
        checkOrUpdate: true, // 编辑的时候不需要加验证 故此处可始终为true
        skuTempName: '', // 临时暂时的sku名称
        categoryId: '', // 分类ID
        categoryPlatList: [],
        categoryName: '', // 分类名称
        // 商品详情图  imgUrl: 图片地址  sortNum: 序号 goodsId: 商品编码
        detailImageList: [], 
        detailImageUrl: '', // 商品详情图地址
        qualityPeriod: '', // 保质期
        // 商品图  imgUrl: 图片地址  sortNum: 序号 goodsId: 商品编码
        imageList: [], 
        initStock: '', // 初始库存
        marketPrice: '', // 市场价
        pack: '', // 包装
        origin: '', // 产地
        goodsDesc: '', // 商品描述
        goodsId: '', // 商品id
        sortNum: '', // 序号
        skuId: '', // SKU_ID
        skuName: '', // SKU 名称
        skuNo: '', // SKU 编号
        spec: '', // 规格
        weight: '', // 重量
        buyName: '', // 商品卖点
        goodsErpSkuRelations: [] // 关联商品
      },
      imageListArr: [],
      currentUpFileIdx: -1, // 默认选中的文件上传框
      // 商品详情图加载
      imageUploadLoading: false
      
    }
  },
  components: {
    'draggable': Draggable
  },
  methods: {
    /**
     * [setDefaultMesg 设置弹框默认属性]
     * @param {[type]} _params [自定义参数]
     */
    setDefaultMesg (_params) {
      this.isShow = true
      if (_params.title) {
        this.title = _params.title
      }
      if(_params.goodsId) {
        this.goodsId = _params.goodsId 
        this.getLibraryDetil(_params.goodsId)
      }
    },
    /**
     * [getLibraryDetil 获取商品详情]
     * @param  {[type]} _goodsId [description]
     * @return {[type]}            [description]
     */
    getLibraryDetil(_goodsId) {
      let goodsId = _goodsId
      this.loading = true
      this.$http.post(requestUrl.libraryDetail, {goodsId: goodsId, DIFFCITY: true}).then(res => {
        this.loading = false
        if(res.code === 0) {
          if(res.data) {
            let createGoodsMesg = res.data
            this.addParamMesg.goodsId = createGoodsMesg.id
            this.addParamMesg.skuTempName = createGoodsMesg.skuNo + '-' + createGoodsMesg.skuName + '-' + createGoodsMesg.spec
            this.addParamMesg.skuName = createGoodsMesg.skuName
            this.addParamMesg.skuNo = createGoodsMesg.skuNo
            this.addParamMesg.skuId = createGoodsMesg.skuId
            this.addParamMesg.goodsName = createGoodsMesg.goodsName
            this.addParamMesg.spec = createGoodsMesg.spec

            this.addParamMesg.goodsErpSkuRelations = createGoodsMesg.goodsItems && createGoodsMesg.goodsItems.length > 0 ? createGoodsMesg.goodsItems : []

            this.addParamMesg.categoryId = createGoodsMesg.categoryId
            if(createGoodsMesg.categoryPlatList && createGoodsMesg.categoryPlatList.length > 0) {
              createGoodsMesg.categoryPlatList = createGoodsMesg.categoryPlatList.map(item => item && parseInt(item))
              this.addParamMesg.categoryPlatList = createGoodsMesg.categoryPlatList
            }
            this.addParamMesg.categoryId = this.addParamMesg.categoryPlatList[this.addParamMesg.categoryPlatList.length - 1]
            this.addParamMesg.categoryName = createGoodsMesg.categoryName

            this.addParamMesg.marketPrice = createGoodsMesg.marketPrice
            this.addParamMesg.sortNum = createGoodsMesg.sortNum
            this.addParamMesg.weight = createGoodsMesg.weight
            this.addParamMesg.buyName = createGoodsMesg.buyName
            this.addParamMesg.goodsDesc = createGoodsMesg.goodsDesc
            this.addParamMesg.pack = createGoodsMesg.pack
            this.addParamMesg.origin = createGoodsMesg.origin
            this.addParamMesg.initStock = createGoodsMesg.initStock
            this.addParamMesg.qualityPeriod = createGoodsMesg.qualityPeriod

            // 编辑的时候返回的内容中缺少  商品方图，商品详情图片，店内分类的list
            if(createGoodsMesg.detailImageList && createGoodsMesg.detailImageList.length > 0) {
              this.addParamMesg.detailImageList = createGoodsMesg.detailImageList
              this.addParamMesg.detailImageUrl = createGoodsMesg.detailImageList[0].imgUrl
            }

            if(createGoodsMesg.imageList && createGoodsMesg.imageList.length > 0) {
              this.imageListArr = createGoodsMesg.imageList
            }else {
              this.imageListArr = []
            }
            this.imageListArr.push({"imgUrl": "", "sortNum": ""})
          }
          return 
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.loading = false
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    },
    /**
     * [datadragEnd 拖拽结束]
     * @return {[type]} [description]
     */
    datadragEnd(evt) {
      for(let [index, elem] of new Map(this.imageListArr.map((item, i) => [i, item]))){
        elem.sortNum = index + 1
      }

      this.imageListArr = this.imageListArr.filter(item => {
        return item.imgUrl != ''
      });

      let imageListItem = {
        imgUrl: '',
        sortNum: ''
      }
      this.imageListArr.push(imageListItem)
    },
    /**
     * [emitUpload 上传图片]
     * @param  {[type]} e [description]
     * @return {[type]}   [description]
     */
    emitUpload(e) {
      if(this.imageListArr.length > 6 && this.currentUpFileIdx != -1) {
        this.$message.error('商品方图，最多可添加6张')
        return
      }
      this.imageUploadLoading = true
      this.getToken().then((res) => {
        // 开始上传图片
        let uploader = qiniu.upload(e.file, res.fileName, res.token)
        uploader.subscribe({
          error: () => {
            this.imageUploadLoading = false
          },
          complete: (res) => {
            // 添加内容设置图片地址
            let urlImage = 'http://retailimg01.benlailife.com/' + res.key
            this.imageUploadLoading = false
            // 设置对应的图片地址
            this.setImgUrl(this.currentUpFileIdx, urlImage)
          },
          next: res => {
          }
        })
      })
    },
    /**
     * [setImgUrl 设置对应的图片路径]
     * @param {[type]} _idx    [description]
     * @param {[type]} _imgUrl [description]
     */
    setImgUrl(_idx, _imgUrl) {
      let urlImage = _imgUrl
      let currentIdx = _idx
      for(let [i, item] of new Map(this.imageListArr.map((item, i) => [i, item]))) {
        if(i === currentIdx) {
          this.imageListArr[i].imgUrl = urlImage
          this.imageListArr[i].sortNum = parseInt(currentIdx) + 1
        }
      }
      let hash = {}
      let imageListItem = {
        imgUrl: '',
        sortNum: ''
      }
      this.imageListArr.push(imageListItem)

      // 去重  防止多个空对象
      this.imageListArr = this.imageListArr.reduceRight((item, next) => {
        hash[next.imgUrl] ? '' : hash[next.imgUrl] = true && item.push(next)
        return item
      }, [])
      this.imageListArr.reverse()
    },
    /**
     * [imageUploadReg 上传图片检测]
     * @param  {[type]} file [description]
     * @return {[type]}      [description]
     */
    imageUploadRegForMore (file) {
      const isPic = file.type.indexOf('image/') !== -1
      const isJPG = file.type === 'image/jpeg' || 'image/png'
      const isSizeNum = file.size <= 1 * 230 * 1024
      if (!isPic) {
        this.$message.error('请上传大小不超过230KB的800*800像素的JPG或PNG图片，最多可添加6张')
        return false
      }
      if (!isJPG) {
        this.$message.error('请上传大小不超过230KB的800*800像素的JPG或PNG图片，最多可添加6张')
        return false
      }
      if (!isSizeNum) {
        this.$message.error('请上传大小不超过230KB的800*800像素的JPG或PNG图片，最多可添加6张')
        return false
      }
      /**
       * [检测上传图片宽高]
       */
      const isSize = new Promise(function(resolve, reject) {
        let width = 800
        let height = 800 

        let _URL = window.URL || window.webkitURL
        let img = new Image();
        img.onload = function() {
          let valid = img.width == width && img.height == height
          valid ? resolve() : reject()
        }
        img.src = _URL.createObjectURL(file)
      }).then(() => {
        return file
      }, () => {
        this.$message.error('请上传大小不超过230KB的800*800像素的JPG或PNG图片，最多可添加6张')
        return Promise.reject()
      })
      return isPic && isJPG && isSizeNum && isSize
    },
    /**
     * [getToken 通过后台封装的接口获取七牛的token]
     * @return {[type]}            [description]
     */
    getToken () {
      return this.$http.post(fileUrlList.getQiNiuToken, {}).then(res => {
        if (res.code == 0) {
          let token = res.data.token
          let fileName = res.data.key
          return {fileName, token}
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.imageUploadLoading = false
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    },
    /**
     * [setItemIdx 设置当前的上传文件的idx]
     * @param {[type]} _idx [description]
     */
    setItemIdx(_idx) {
      this.currentUpFileIdx = _idx
    },
    /**
     * [removeFileLeft 删除]
     * @param  {[type]} _idx [description]
     * @return {[type]}       [description]
     */
    deleteUpFile(_idx) {
      let idx = _idx
      this.imageListArr = this.imageListArr.filter((item, idx) => {
        return idx != _idx
      })
    },
    /**
     * [doConfirmHandel 点击确定]
     * @return {[type]} [description]
     */
    doConfirmHandel() {
      // 处理要上传的商品方图
      let imageListArr = this.imageListArr.filter((item, idx) => {
        return item.imgUrl != '' 
      })
      for(let [i, item] of new Map(imageListArr.map((item, i) => [i, item]))) {
        item.sortNum = parseInt(i) + 1
      }
      this.addParamMesg.imageList = imageListArr
      if(this.addParamMesg.imageList.length === 0) {
        this.$message("请添加商品方图")
        return 
      }

      this.disabled = true
      this.saveLoading = true
      this.$http.post(requestUrl.updateOrInsert, {...this.addParamMesg}).then(res => {
        // 保存按钮可再次点击
        setTimeout(() => {
          this.disabled = false
          this.saveLoading = false
        }, 800)
        if(res.code === 0) {
          this.isShow = false
          this.$message.success("添加商品图片成功")
          this.$emit("doSearchHandel")
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        // 保存按钮可再次点击
        setTimeout(() => {
          this.disabled = false
          this.saveLoading = false
        }, 800)
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })

    } 
  }
}
</script>



